@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
@import url(https://highlightjs.org/static/demo/styles/railscasts.css);
*{margin:0;padding:0;box-sizing:border-box}body{background-color:antiquewhite;font-family:'Roboto', 'Oxygen', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}

.app{display:flex;flex-direction:column;align-items:center;justify-content:center;width:80vw;margin:2vh auto;padding:10vh 0;text-align:center}.app-header{font-size:5vh;color:#e4602b;text-shadow:0.12vw 0.12vh 0 #333}.md-io-wrapper{padding:5vh 0;display:grid;grid-template-columns:1fr 1fr;align-items:center;justify-content:center}@media screen and (max-width: 520px){.app{width:100vw;margin:0 auto;padding:2vh 0}.md-io-wrapper{padding-top:2vh;grid-template-columns:1fr}}

.editor{height:40vh;width:35vw;text-shadow:none;text-align:left;text-align:initial;padding-left:2em;font-size:0.85em;line-height:1.6em}@media screen and (max-width: 520px){.editor{height:50vh;width:100vw}}

.preview{height:40vh;width:35vw;background:#232323;overflow-y:scroll;font-size:0.85em;line-height:1.6em;text-shadow:none;text-align:left;text-align:initial;color:#ddd;padding:1em 0em 1em 1.5em}@media screen and (max-width: 520px){.preview{height:50vh;width:100vw;padding-left:0.5em;padding-bottom:2em}}

